body,
#root {
	height: 100vh;
	background: #f4e8c1;
	background-size: cover;
}

.App {
	max-width: 600px;
	min-width: 320px;
	min-height: 320px;
	height: 100%;
	margin: 0 auto;
	font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
}

form {
	display: flex;
	padding: 0 1rem;
	height: 20%;
	max-height: 120px;
	align-items: center;
}

#submit {
	width: 56px;
	height: 56px;
	margin-left: 0.5rem;
	box-sizing: border-box;
	padding: 0;
	border: none;
	border-radius: 4px;
	text-transform: uppercase;
	color: white;
	font-size: 1.1rem;
	font-family: inherit;
	background-color: #3f51b5;
	transition: background-color 0.1s ease-in-out;
	flex-shrink: 0;
}

#submit:hover,
#submit:focus {
	outline: none;
	background-color: #2b2399;
}

.weather-card {
	padding: 0 1rem 2rem;
	box-sizing: border-box;
	height: 80%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	align-content: space-between;
	justify-content: space-between;
}

.weather-card div {
	text-align: center;
	align-self: center;
}

.location {
	text-transform: uppercase;
	font-weight: 400;
	margin: 0;
}

.weather-card h2,
.weather-card p {
	color: white;
	text-shadow: rgba(0, 0, 0, 0.8) 0px 1px 8px;
}

.weather-card p {
	font-size: 1.5rem;
	margin: 0.5rem 0;
}

.switch {
	align-self: flex-end;
	position: relative;
	display: inline-block;
	width: 60px;
	height: 34px;
}

.switch input {
	opacity: 0;
	width: 0;
	height: 0;
}

.toggle {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #3f51b5;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	transition: 0.4s;
	border-radius: 26px;
}

.toggle:before {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #3f51b5;
	font-weight: 700;
	content: '\00B0 C';
	height: 26px;
	width: 26px;
	left: 4px;
	bottom: 4px;
	background-color: white;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	transition: 0.4s;
	border-radius: 26px;
}

input:checked + .toggle:before {
	content: '\00B0 F';
	-webkit-transform: translateX(26px);
	-ms-transform: translateX(26px);
	transform: translateX(26px);
}

.loader {
	margin: 3rem auto;
	border: 5px solid #f3f3f3;
	border-top: 5px solid #3f51b5;
	border-radius: 50%;
	width: 70px;
	height: 70px;
	animation: spin 2s linear infinite;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

.error-screen {
	color: #3f51b5;
	padding: 1rem;
}

.error-screen h1 {
	margin: 0;
}

div.MuiFilledInput-root {
	background-color: rgba(255, 255, 255, 0.4);
}

div.MuiFilledInput-root:hover,
div.MuiFilledInput-root:focus,
div.MuiFilledInput-root:active,
div.MuiFilledInput-root.Mui-focused {
	background-color: rgba(255, 255, 255, 0.8);
}
